@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -webkit-transition-property: color, background-color, width;
    -webkit-transition-duration: 0.75s;
    -webkit-transition-timing-function: ease;
    transition-property: color, background-color, width;
    transition-duration: 0.75s;
    transition-timing-function: ease;
}

body {
    font-family: 'Ubuntu', sans-serif;
}

.sensorArray {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.sensor {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.sensor {
    min-width: 25%;
    float: left;
}

h1 {
    color: white;
    text-align: center;
    padding-left: 0.175em;
    padding-right: 0.175em;
    font-size: 10em;
}

.dark {
    background-color: #424242;
    color: white;
}

.light {
    background-color: #F2F2F2;
    color: #282828;
}

.cold {
    background-color: #43A1FE;
    color: white;
}

.hot {
    background-color: #FE9241;
    color: white;
}

.good {
    background-color: #26ED58;
    color: #282828;
}

h2 {
    color: white;
    text-align: center;
    font-size: 3.5em;
    padding-bottom: 0.175em;
    padding-left: 0.175em;
    padding-right: 0.175em;
    width: 100%;
}

h2.dark {
    background-color: #282828;
}

h2.light {
    background-color: #DBDBDB;
    color: #282828;
}

h2.cold {
    background-color: #268AED;
}

h2.hot {
    background-color: #ED7728;
}

h2.good {
    background-color: #0FBD3B;
    color: #282828;
}




.valueDisplay {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bolder;
    font-size: 5em;
    outline: 0;
    width: 100%;
    -webkit-flex-grow: 2;
            flex-grow: 2;
}

.valueDisplay.dark {
    border: 5px solid white;
    color: white;
}

.valueDisplay.light {
    border: 5px solid #282828;
    color: #282828;
}

.valueDisplay.good {
    border: 5px solid #0FBD3B;
    color: #282828;
}

.valueDisplay.hot {
    border: 5px solid #ED7728;
    color: white;
}

.valueDisplay.cold {
    border: 5px solid #268AED;
    color: white;
}

.btn-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bolder;
    font-size: 5em;
    min-width: 20%;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

div.btn-wrapper:hover {
    cursor: pointer;
}

.btn-wrapper.red{
    background-color: #EE2B2B;
    border: 5px solid #BD0F0F;
    color: white;
}

.btn-wrapper.red:hover:not(.disabled){
    background-color: #BD0F0F;
}

.btn-wrapper.green{
    background-color: #26ED58;
    border: 5px solid #0FBD3B;
    color: black;
}

.btn-wrapper.green:hover:not(.disabled){
    background-color: #0FBD3B;
}

.btn-wrapper.disabled{
    background-color: #C0C0C0;
    border: 5px solid #888;
    color: white;
}

h3 {
    padding-bottom: 0.5em;
    text-align: center;
    font-size: 2em;
}

.adjuster {
    display: -webkit-flex;
    display: flex;
    width: 66%;
    margin: auto;
    padding-bottom: 1em;
